.step-counter {
  background: black;
  padding: 10px;
  float: left;
}

.game {
  display: flex;
  flex-direction: row;
  .rotated-board{
    transform: perspective(471px) rotateX(57deg) rotateY(-3deg) rotateZ(60deg);
  }
  &-info {
    margin-left: 20px;
  }
}

.selected {
  display: block;
  border: 1px solid #555;
  padding: 10px;
}

.button {
  color: #999;
  background: #444;
  margin-top: 5px;
  margin-right: 5px;
  border: 2px solid #666;
  padding: 5px;
  &-nuke {
    color: #E00;
  }
}
